import classNames from 'classnames';
import React from 'react';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { actions as currTodoActions } from '../../features/currentTodo';
import { Todo } from '../../types/Todo';

type Props = {
  todo: Todo;
};

export const TodoItem: React.FC<Props> = ({ todo }) => {
  const currentTodo = useAppSelector((state) => state.currentTodo);
  const dispatch = useAppDispatch();
  const { id, title, completed } = todo;

  return (
    <tr
      data-cy="todo"
      key={id}
      className={classNames({
        'has-background-info-light': todo.id === currentTodo?.id,
      })}
    >
      <td className="is-vcentered">{id}</td>
      <td className="is-vcentered">
        {completed && (
          <span className="icon" data-cy="iconCompleted">
            <i className="fas fa-check" />
          </span>
        )}
      </td>
      <td className="is-vcentered is-expanded">
        <p
          className={classNames({
            'has-text-success': completed,
            'has-text-danger': !completed,
          })}
        >
          {title}

        </p>
      </td>
      <td className="has-text-right is-vcentered">
        <button
          data-cy="selectButton"
          className="button"
          type="button"
          onClick={() => {
            dispatch(
              currentTodo?.id === todo.id
                ? currTodoActions.removeTodo()
                : currTodoActions.setTodo(todo),
            );
          }}
        >
          <span className="icon">
            <i className={classNames(
              'far',
              { 'fa-eye': id !== currentTodo?.id },
              { 'fa-eye-slash': id === currentTodo?.id },
            )}
            />
          </span>
        </button>
      </td>
    </tr>
  );
};
